.container {
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow-x: hidden;
   overflow-y: scroll;

   :global .title {
      font-size: 36px;
      text-align: center;
      margin-top: 5px;
      margin-bottom: 10px;
   }

   :global .entry {
      border-radius: 12px;
      background-color: #2a2a2a;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      width: calc(100% - 20px);
      margin-bottom: 8px;
      padding: 12px;
      font-size: 20px;
   }
}