.pair {
   display: flex;
   flex-direction: column;
   align-items: center;

   &:not(:first-child) {
      margin-top: 3px;
   }

   :global .overview {
      background-color: #2a2a2a;
      border-radius: 10px;
      width: 100%;
      padding: 10px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      cursor: pointer;

      :global span:not(:first-child):not(:last-child) {
         text-align: center;
      }

      :global span:last-child {
         text-align: right;
      }
   }

   :global .trade_list {
      display: flex;
      flex-direction: column;
      width: 95%;
      background-color: #212121;
      border-radius: 0px 0px 10px 10px;

      :global .trade {
         border-top: 1px solid #262626;
         padding: 10px;
         display: grid;
         grid-template-columns: 1fr 1fr 1fr 1fr 50px;
         cursor: pointer;
   
         :global span:not(:first-child):not(:last-child) {
            text-align: center;
         }

         :global span:last-child {
            text-align: right;
         }
      }
   }
}