.container {
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow-x: hidden;
   overflow-y: scroll;

   :global .title {
      font-size: 24px;
      text-align: center;
      margin-top: 5px;
   }

   :global .balance-container {
      display: flex;
      flex-direction: row;
      align-items: flex-end;

      :global .balance {
         font-size: 32px;
      }

      :global .profit {
         font-size: 14px;
         margin-bottom: 6px;
         margin-left: 2px;
      }
   }

   :global .portfolio {
      font-size: 24px;
   }

   :global .pie-container {
      width: 150px;
      height: 150px;
      margin-bottom: 20px;
      margin-top: 5px;
   }

   :global .entry {
      border-radius: 12px;
      background-color: #2a2a2a;
      display: flex;
      flex-direction: row;
      align-items: center;
      width: calc(100% - 20px);
      margin-bottom: 8px;
      padding: 12px;
      font-size: 20px;

      :global .color {
         width: 20px;
         height: 20px;
         border-radius: 50%;
         margin-right: 5px;
      }
   }
}