* {
   box-sizing: border-box;
}

body {
   background-color: #1a1a1a;
   color: #f1f1f1;
   font-family: Roboto;
   height: 100%
}

html {
   height: 100%;
   -webkit-tap-highlight-color: transparent;
}

#root {
   height: 100%;
}

.outer_container {
   width: 100%;
   height: 100%;
   display: grid;
   grid-template-columns: 1fr;
   grid-template-rows: auto 1fr 50px;
   grid-template-areas:
   "dropdown"
   "content"
   "menu";

   :global .dropdown {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-top: 5px;

      :global .icon {
         width: 25px;
         height: 25px;
         mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/paper-plane.svg) no-repeat center;
         -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/paper-plane.svg) no-repeat center;
         background-color: #42c2f4; //#ef9c37;
         margin-right: 5px;
      }

      :global .instance_select {
         grid-area: dropdown;
         :global .Dropdown-control {
            background-color: transparent;
            color: inherit;
            border: none;
            height: 20px;
            padding: 0px 20px 0px 0px;
            cursor: pointer;
   
            :global .Dropdown-arrow {
               top: 50%;
               right: 5px;
               transform: translateY(-50%);
            }
         }
   
         :global .Dropdown-menu {
            border: 1px solid #2a2a2a;
            width: max-content;
            :global .Dropdown-option {
               background-color: #1f1f1f;
               color: inherit;
   
               &:last-child {
                  border-radius: 0px;
               }
   
               &:hover {
                  background-color: #252525;
               }
            }
         }
      }
   }
   
   :global .menu {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      grid-area: menu;
      background-color: #111;
      align-items: center;

      :global .menuitem {
         display: flex;
         flex-direction: column;
         align-items: center;
         color: #7c7c7c;
         text-decoration: none;
         width: 50px;
         
         :global .icon {
            width: 25px;
            height: 25px;
            background-color: #7c7c7c;
         }

         :global .label {
            font-size: 12px;
         }

         &.active .icon {
            background-color: #0a84ff;
         }

         &.active .label {
            color: #42c2f4;
         }

         &.balance .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet-outline.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet-outline.svg) no-repeat center;
         }

         &.balance.active .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet.svg) no-repeat center;
         }

         &.info .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/information-circle-outline.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/information-circle-outline.svg) no-repeat center;
         }

         &.info.active .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/information-circle.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/information-circle.svg) no-repeat center;
         }

         &.trades .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/bar-chart-outline.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/bar-chart-outline.svg) no-repeat center;
         }

         &.trades.active .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/bar-chart.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/bar-chart.svg) no-repeat center;
         }

         &.pairs .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/link-outline.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/link-outline.svg) no-repeat center;
         }

         &.pairs.active .icon {
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/link.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/link.svg) no-repeat center;
         }
      }
   }
}

::-webkit-scrollbar {
   display: none;
}