.container {
   display: flex;
   flex-direction: column;
   align-items: center;
   position: relative;

   :global .daily_header {
      font-size: 24px;
   }

   :global .customtooltip {
      display: flex;
      flex-direction: column;
      background-color: #2a2a2a;
      border-color: #2f2f2f;
      padding: 10px;
      color: #42c2f4;

      :global .label {
         color: #f1f1f1;
      }
   }

   :global .selector {
      position: absolute;
      top: 10px;
      right: 0px;
      display: flex;
      flex-direction: row;
      background-color: #2a2a2a;
      border-radius: 5px;

      :global .selection {
         padding: 2px 10px;
         font-size: 12px;
         display: flex;
         justify-content: center;
         align-items: center;
         user-select: none;
         cursor: pointer;
      }

      :global .selected {
         color: #42c2f4;
      }
   }
}