.page_layout {
   display: grid;
   width: 1080px;
   height: 600px;
   border: 1px solid #2a2a2a;
   border-radius: 15px;
   padding: 10px;
   gap: 10px;
   grid-template-columns: repeat(2, 1fr);
   grid-template-rows: repeat(2, 1fr);
   grid-template-areas:
      "info daily"
      "info pairs";
}