.container {
   display: grid;
   gap: 10px;
   grid-template-columns: repeat(3, 1fr);
   grid-template-rows: repeat(6, 1fr);

   :global .info {
      display: flex;
      flex-direction: column;
      border-radius: 10px;
      background-color: #2a2a2a;
      padding: 5px;

      :global .label {
         font-size: 12px;
         color: #9f9f9f;
      }

      :global .value {
         font-size: 36px;
         text-align: center;
      }

      :global .extra {
         font-size: 14px;
         text-align: center;
      }
   }
}