.container {
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow-x: hidden;
   overflow-y: scroll;

   :global .title {
      font-size: 36px;
      text-align: center;
      margin-top: 5px;
      margin-bottom: 10px;
   }

   :global .entry {
      border-radius: 12px;
      background-color: #2a2a2a;
      align-items: center;
      width: calc(100% - 20px);
      margin-bottom: 8px;
      padding: 12px;
      font-size: 20px;

      display: grid;
      grid-template-rows: repeat(3, auto);
      grid-template-columns: repeat(3, 1fr);

      :global .small {
         font-size: 12px;
      }

      :global .big {
         font-size: 26px;
      }

      :global .center {
         text-align: center;
      }

      :global .right {
         text-align: right;
      }
   }
}