.container {
   display: flex;
   flex-direction: column;
   align-items: center;
   overflow: hidden;

   :global .daily_header {
      font-size: 24px;
   }

   :global .headers {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      width: 100%;
      padding: 0px 10px 0px 10px;
      margin-top: 10px;

      :global span:not(:first-child):not(:last-child) {
         text-align: center;
      }

      :global span:last-child {
         text-align: right;
      }
   }

   :global .pair_list {
      display: flex;
      flex-direction: column;
      overflow-y: scroll;
      margin-top: 2px;
      border-radius: 10px;
   }
}